.SSO-page .shared-banner{
    clip-path: polygon(0 0, 100% 0, 100% 85%, 65% 100%, 0 85%);
    position: relative;
    overflow:visible;
    z-index: 1;
    padding:11% 8%;
    h1{
        font-size: 42px;
    }
}
.SSO-page{
    overflow: hidden;
}
@media(max-width:1401px){
    .SSO-page .shared-banner{
        clip-path: polygon(0 0, 100% 0, 100% 85%, 65% 100%, 0 85%);
        h1{
            font-size: 33px;
        }
    }
}

@media(max-width:767px){
    .SSO-page .shared-banner{
        clip-path: polygon(0 0, 100% 0, 100% 90%, 65% 100%, 0 85%);
        padding:130px 7.5% 65px;
        h1{
            font-size: 22px;
        }
    }
}

//==================================================
.SSO-page .whats-SSO{
    margin:5% 0 7.5%;
    padding:0 10%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .info{
        width: 37.5%;
    }
    h1{
        font-size: 35px;
        margin-bottom: 20px;
    }
    p{
        font-size: 19px;
    }
    .images{
        width: 48%;
        position: relative;
    }
    .top-img{
        content: url('../../../../assets/SSO/img1.png');
        width: 80%;
        position: relative;
        z-index: 2;
        transition: 0.35s ease-in-out;
        &:hover{
            transform: scale(1.035);
            filter: brightness(0.925);
        }
    }
    .bg-img{
       width: 50%;
       content: url('../../../../assets/SSO/bg1.png');
       position: absolute;
       right: 0;
       top:17.5%;
    }
}

@media(max-width:1401px){
    .SSO-page .whats-SSO{
        .info{
            width: 40%;
        }
        h1{
            font-size: 27px;
            margin-bottom: 15px;
        }
        p{
            font-size: 16px;
            line-height: 1.5;
        }
    }
}

@media(max-width:991px){
    .SSO-page .whats-SSO{
        padding:0 7.5%;
        .info{
            width: 45%;
        }
    }
}

@media(max-width:767px){
    .SSO-page .whats-SSO{
       flex-wrap: wrap;
       margin:25px 0;
        .info{
            width: 100%;
            margin:20px 0;
        }
        h1{
            font-size: 20px;
        }
        p{
           line-height: 1.4;
           font-size: 15px;
        }
        .images{
            width: 96%;
            margin:20px auto;
        }
    }
}