.shared-trusted-business{
    background-image: url('../../../assets/Home/business-bg.png');
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding:3.5% 7.5% 5%;
    p{
        font-size: 20px;
        text-align: center;
        margin-bottom: 35px;
    }
    .images{
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .img{
        height: 40px;
        transition: 0.3s ease-in-out;
        &:hover{
            filter: brightness(0.6);
            transform: scale(1.035);
        }
    }
    .img1{
        content: url('../../../assets/Home/business1.png');
    }
    .img2{
        content: url('../../../assets/Home/business2.png');
    }
    .img3{
        content: url('../../../assets/Home/business3.png');
    }
    .img4{
        content: url('../../../assets/Home/business4.png');
    }
    .img5{
        content: url('../../../assets/Home/business5.png');
    }
    .img6{
        content: url('../../../assets/Home/business6.png');
    }
    .img7{
        content: url('../../../assets/Home/business7.png');
    }
}

@media(max-width:1401px){
    .shared-trusted-business{
        p{
            font-size: 16px;
            margin-bottom: 30px;
        }
        .img{
            height: 30px;
        }
    }
}

@media(max-width:991px){
    .shared-trusted-business{
        .img{
            height: 25px;
        }
    }
}
@media(max-width:830px){
    .shared-trusted-business{
        .images{
            justify-content: center;
        }
        .img{
            margin:15px 20px;
        }
    }
}

@media(max-width:500px){
    .shared-trusted-business{
        padding:35px 7.5%;
        p{
            font-size: 16px;
            margin-bottom: 10px;
        }
    }
}
