.SSO-page .benefits .row1{
    margin:7.5% 0 5%;
    padding:0 10%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    .images{
        width: 45%;
        position: relative;
    }
    .top-img{
        content: url('../../../../assets/SSO/img2.png');
        width: 100%;
        position: relative;
        z-index: 2;
    }
    .bg-img{
        content: url('../../../../assets/SSO/bg2.png');
        position: absolute;
        left: -15%;
        top:-25%;
        width: 50%;
    }
    .info{
        width: 42.5%;
    }
    h1{
        font-size: 35px;
    }
    .p{
        font-size: 18px;
        margin:20px 0;
    }
}

@media(max-width:1401px){
    .SSO-page .benefits .row1{
        padding:0 7.5%;
        .images{
            width: 45%;
        }
        .bg-img{
            left: -12.5%;
        }
        .info{
            width: 45%;
        }
        h1{
            font-size: 25px;
        }
        .p{
            font-size: 15px;
            margin:15px 0;
        }
    }
}

@media(max-width:991px){
    .SSO-page .benefits .row1{
        margin:25px 0 5px;
        .images{
            width: 525px;
            max-width: 85%;
            margin:55px auto 20px;
        }
        .bg-img{
            left: -15%;
            top:-25%;
            width: 50%;
        }
        .info{
            width: 555px;
            max-width: 100%;
            margin:20px auto;
        }
        h1{
            font-size: 20px;
        }
    }
}

//=========================================================

.SSO-page .benefits .row2{
    margin:5% 0;
    padding:0 10%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    .col1{
        width: 40%;
    }
    .col2{
        width: 42.5%;
        position: relative;
    }
    h1{
        font-size: 33px;
        margin-bottom: 30px;
    }
    .item1{
        display: flex;
        align-items: center;
        margin:20px 0;
        p{
            flex: 1;
            font-size: 17px;
        }
    }
    .icon{
        height: 35px;
        margin-right: 20px;
    }
    a{
        width: 100%;
        position: relative;
        z-index: 2;
        display: flex;
        align-items: center;
        justify-content: center;
        svg{
            position: absolute;
            top:50%;
            left:50%;
            transform: translate(-50%,-50%);
            color:#fff;
            font-size: 100px;
        }
        &:hover{
            svg{
                color:#90b1ff;
            }
        }
    }
    .bg-img{
        content: url('../../../../assets/SSO/bg3.png');
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%, -50%);
        width: 120%;
    }
    .poster{
        content: url('../../../../assets/SSO/poster.png');
        width: 100%;
        border-radius: 30px;
    }
}

@media(max-width:1401px){
    .SSO-page .benefits .row2{
        margin:7.5% 0 5%;
        .col1{
            width: 40%;
        }
        .col2{
            width: 42.5%;
        }
        h1{
            font-size: 25px;
            margin-bottom: 25px;
        }
        .item1{
            margin:15px 0;
            p{
                font-size: 15px;
            }
        }
        .icon{
            height: 30px;
            margin-right: 20px;
        }
        a{
            svg{
                font-size: 80px;
            }
        }
        .poster{
            border-radius: 30px;
        }
    }
}

@media(max-width:991px){
    .SSO-page .benefits .row2{
        margin:5px 0 25px;
        .col1{
           width: 500px;
           max-width: 100%;
           margin:20px auto;
        }
        .col2{
            width: 500px;
            max-width: 100%;
            margin:20px auto;
        }
        h1{
            font-size: 20px;
            margin-bottom: 20px;
        }
        .item1{
            margin:15px 0;
            p{
                font-size: 15px;
            }
        }
        .icon{
            height: 30px;
            margin-right: 20px;
        }
        a{
            svg{
                font-size: 80px;
            }
        }
        .poster{
            border-radius: 30px;
        }
    }
}